import React from "react";
import style from "./ReadPage.module.scss"
import {useQuery} from "@apollo/client";
import {IProduct} from "../../types/product.type";
import {Link} from "react-router-dom";
import {GET_PRODUCTS} from "../../api/api";
import {LinearPreloader} from "../X_Common/LinearPreloader/LinearPreloader";

export const ReadPage = () => {
    const {loading, error, data} = useQuery<{ products: IProduct[] }>(
        GET_PRODUCTS,
        {
            //fetchPolicy: "cache-first", // (-),        
            fetchPolicy: "network-only", //     ,   
            nextFetchPolicy: "cache-first" //    
        }
    );

    return (
        <div className={style.readPage}>

            {loading && !error && <LinearPreloader/>}

            <h1 className={style.title}>Read - Products list</h1>

            {
                data && (
                    <div className={style.products}>
                        {
                            data.products.map(({id, name, color, count}) => (
                                <Link key={id}
                                      to={`/read/${id}`}
                                      className={style.link}
                                >
                                    {name}
                                </Link>
                            ))
                        }
                    </div>
                )
            }
        </div>
    )
}
