import React, {useState} from "react";
import style from "./ProductPage.module.scss"
import {ApolloError, useMutation, useQuery} from "@apollo/client";
import {Link, Navigate, useParams} from "react-router-dom";
import {CreateProductErrorType, CreateProductType, IProduct} from "../../types/product.type";
import {GET_PRODUCT_BY_ID, UPDATE_PRODUCT} from "../../api/api";
import {useStore} from "../../store/useStore";
import {observer} from "mobx-react-lite";
import {useFormik} from "formik";
import {Button, TextField} from "@mui/material";
import {LinearPreloader} from "../X_Common/LinearPreloader/LinearPreloader";

export const ProductPage = observer(() => {
    const {authStore: {auth}} = useStore();

    const {appStore: {setInfoPopup}} = useStore();

    const {id} = useParams<{ id: string }>();

    const {loading, error, data} = useQuery<{ product: IProduct }>(
        GET_PRODUCT_BY_ID,
        {
            variables: {id}
        }
    );

    const [
        updateProduct,
        {loading: loadingUpdate, error: errorUpdate}
    ] = useMutation<{ updateProduct: IProduct }>(
        UPDATE_PRODUCT, {
            refetchQueries: [
                {
                    query: GET_PRODUCT_BY_ID,
                    variables: {id}
                },
            ],

            // update: (cache: any, mutationResult: FetchResult) => {
            //     console.log(cache)
            //     console.log(mutationResult)
            // },

            onError: (error: ApolloError) => {
                // console.log(error)
                // setInfoPopup({open: true, severity: "error", text: error.message})
            },

            onCompleted: (data) => {
                setInfoPopup({open: true, severity: "success", text: `${data.updateProduct.name} is updated`})
            }
        }
    );

    const [showForm, setShowForm] = useState(false);

    const validate = (values: CreateProductType) => {
        const error = {} as CreateProductErrorType
        if (!values.name) {
            error.name = "required"
        }
        return error
    }

    const onSubmit = async (values: CreateProductType) => {
        try {
            updateProduct({
                variables: {
                    updateProductData: {id, color: "red", ...values}
                }
            })
        } catch (e: any) {
            console.log(e)
        } finally {
            //formik.resetForm()
        }
    }

    const formik = useFormik({
        initialValues: {
            name: data?.product?.name || "",
            count: data?.product?.count || 1
        },
        validate,
        onSubmit,
        enableReinitialize: true,
    })

    if (errorUpdate && errorUpdate.message === "jwt expired") {
        return (
            <Navigate to="/login"/>
        )
    }

    return (

        <div className={style.productPage}>

            {(loading || loadingUpdate) && !error && <LinearPreloader/>}

            <h1 className={style.title}>Read - Product</h1>

            {
                data && (
                    <>
                        <div className={style.navigator}>
                            <Link to="/read">Products</Link>
                            <span> / {data.product.name}</span>
                        </div>

                        <div className={style.product}>
                            <p><span>name:</span> {data.product.name}</p>
                            <p><span>count:</span> {data.product.count}</p>
                            <p><span>color:</span> {data.product.color}</p>
                        </div>

                        {
                            auth &&
                            <Button className={style.btn}
                                    variant="outlined"
                                    size="small"
                                    color={showForm ? "error" : "primary"}
                                    onClick={() => setShowForm(!showForm)}
                            >
                                {showForm ? "close form" : "open form"}
                            </Button>
                        }

                        {
                            showForm &&
                            <form onSubmit={formik.handleSubmit}
                                  className={style.form}
                            >
                                <TextField
                                    className={style.field}
                                    size="small"
                                    fullWidth
                                    id="name"
                                    name="name"
                                    label="Product name"
                                    value={formik.values.name}
                                    onChange={formik.handleChange}
                                    error={formik.touched.name && Boolean(formik.errors.name)}
                                    helperText={formik.touched.name && formik.errors.name}
                                />
                                <TextField
                                    className={style.field}
                                    size="small"
                                    fullWidth
                                    type="number"
                                    id="count"
                                    name="count"
                                    label="Product count"
                                    value={formik.values.count}
                                    onChange={formik.handleChange}
                                    error={formik.touched.count && Boolean(formik.errors.count)}
                                    helperText={formik.touched.count && formik.errors.count}
                                />
                                <Button color="primary"
                                        variant="contained"
                                        fullWidth
                                        type="submit"
                                        size="small"
                                        className={style.btn}
                                >
                                    Update product
                                </Button>
                            </form>
                        }


                    </>
                )
            }
        </div>
    )
})
