export const CreatePage = observer(() => {
    const {appStore: {setInfoPopup}, authStore: {auth}} = useStore();

    const {loading, error, data} = useQuery<{ products: IProduct[] }>(GET_PRODUCTS);

    const [createProduct, {data: createData, loading: createLoading, error: createError}] = useMutation<{ createProduct: IProduct }>(
        CREATE_PRODUCT,
        {
            refetchQueries: [
                {query: GET_PRODUCTS},
            ],
            //    Link
            onError: (error: ApolloError) => {
                //console.log(error)
                //setInfoPopup({open: true, severity: "error", text: error.message})
                //   Link
            },
            onCompleted: (data) => {
                setInfoPopup({open: true, severity: "success", text: `${data.createProduct.name} is created`})
            }
        });

    const initialValues: CreateProductType = {
        name: "",
        count: 1
    }

    const validate = (values: CreateProductType) => {
        const error = {} as CreateProductErrorType
        if (!values.name) {
            error.name = "required"
        }
        return error
    }
    const onSubmit = async (values: CreateProductType) => {
        try {
            await createProduct({
                variables: {
                    createProductData: {color: "red", ...values}
                }
            })
        } catch (e: any) {
            console.log(e) //  ,   onError
        } finally {
            formik.resetForm()
        }
    }
    const formik = useFormik({
        initialValues,
        validate,
        onSubmit
    });

    if (!auth) {
        return (
            <Navigate to="/"/>
        )
    }

    return (
        <TokenErrorWrapper error={createError}>
            <div className={style.createPage}>

                {(loading || createLoading) && !error && !createError && <LinearPreloader/>}

                <h1 className={style.title}>Create product (Updating local data by refetching queries)</h1>

                {
                    data && (
                        <div className={style.list}>
                            {
                                data.products.map(({id, name}) => (
                                    <p key={id}>{name}</p>
                                ))
                            }
                        </div>
                    )
                }

                <form onSubmit={formik.handleSubmit}
                      className={style.form}
                >
                    <TextField
                        className={style.field}
                        size="small"
                        fullWidth
                        id="name"
                        name="name"
                        label="Product name"
                        value={formik.values.name}
                        onChange={formik.handleChange}
                        error={formik.touched.name && Boolean(formik.errors.name)}
                        helperText={formik.touched.name && formik.errors.name}
                    />
                    <TextField
                        className={style.field}
                        size="small"
                        fullWidth
                        type="number"
                        id="count"
                        name="count"
                        label="Product count"
                        value={formik.values.count}
                        onChange={formik.handleChange}
                        error={formik.touched.count && Boolean(formik.errors.count)}
                        helperText={formik.touched.count && formik.errors.count}
                    />
                    <Button color="primary"
                            variant="contained"
                            fullWidth
                            type="submit"
                            size="small"
                            className={style.btn}
                    >
                        Create product
                    </Button>
                </form>

            </div>
        </TokenErrorWrapper>

    )
})
